* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  /* font-family: "Encode Sans Expanded", sans-serif; */
  font-family: "Open Sans", sans-serif;
  /* font-family: 'Montserrat', sans-serif; */
}

.ContactMe {
  margin-right: auto;
  margin-left: auto;
  padding: 0 24px;
  background-color: #5c2c06;
  height: 1081px;
  align-items: center;
  justify-content: center;
  display: flex;
}

h1 {
  color: black;
  margin-bottom: 50px;
}

input {
  padding: 10px;
  margin: 10px 0;
  border-radius: 10px;
}

button {
  border-radius: 50px;
  white-space: nowrap;
  outline: none;
  cursor: pointer;
  display: flex;
  border-color: black;
  justify-content: center;
  align-items: center;
  transition: all 0.2s ease-in-out;
  margin-top: 50px;
  padding: 14px 48px;
  background: white;
  color: #010606;
  font-size: 20px;
}

form {
  background-color: white;
  padding: 150px;
  border-radius: 100px;
  z-index: 3;
}

.container {
  margin-right: auto;
  margin-left: auto;
  padding: 0 24px;
  background-color: gray;
  height: 1081px;
  align-items: center;
  justify-content: center;
  display: flex;
  box-sizing: border-box;
  justify-content: space-between;
}

.box {
  background-color: red;
  height: 50%;
  border-radius: 50px;
  margin: 0 50px;
  max-width: 400px;
}

