.container {
  margin-right: auto;
  margin-left: auto;
  padding: 0 24px;
  background-color: gray;
  height: 1081px;
  align-items: center;
  justify-content: center;
  display: flex;
  box-sizing: border-box;
  justify-content: space-between;
}

.box {
  background-color: red;
  height: 50%;
  border-radius: 50px;
  margin: 0 50px;
  max-width: 400px;
}
